	module.exports = {"pathContext":{"docHtml":"<h1>1.9.0 / 2017-11-24</h1>\n<ul>\n<li>[New] ES2015+: <code>AdvanceStringIndex</code></li>\n<li>[Dev Deps] update <code>eslint</code>, <code>nsp</code></li>\n<li>[Tests] require node 0.6 to pass again</li>\n<li>[Tests] up to <code>node</code> <code>v9.2</code>, <code>v8.9</code>, <code>v6.12</code>; use <code>nvm install-latest-npm</code>; pin included builds to LTS</li>\n</ul>\n<h1>1.9.0 / 2017-09-30</h1>\n<ul>\n<li>[New] <code>es2015+</code>: add <code>ArraySpeciesCreate</code></li>\n<li>[New] ES2015+: add <code>CreateDataProperty</code> and <code>CreateDataPropertyOrThrow</code></li>\n<li>[Tests] consolidate duplicated tests</li>\n<li>[Tests] increase coverage</li>\n<li>[Dev Deps] update <code>nsp</code>, <code>eslint</code></li>\n</ul>\n<h1>1.8.2 / 2017-09-03</h1>\n<ul>\n<li>[Fix] <code>es2015</code>+: <code>ToNumber</code>: provide the proper hint for Date objects (#27)</li>\n<li>[Dev Deps] update <code>eslint</code></li>\n</ul>\n<h1>1.8.1 / 2017-08-30</h1>\n<ul>\n<li>[Fix] ES2015+: <code>ToPropertyKey</code>: should return a symbol for Symbols (#26)</li>\n<li>[Deps] update <code>function-bind</code></li>\n<li>[Dev Deps] update <code>eslint</code>, <code>@ljharb/eslint-config</code></li>\n<li>[Docs] github broke markdown parsing</li>\n</ul>\n<h1>1.8.0 / 2017-08-04</h1>\n<ul>\n<li>[New] add ES2017</li>\n<li>[New] move es6+ to es2015+; leave es6/es7 as aliases</li>\n<li>[New] ES5+: add <code>IsPropertyDescriptor</code>, <code>IsAccessorDescriptor</code>, <code>IsDataDescriptor</code>, <code>IsGenericDescriptor</code>, <code>FromPropertyDescriptor</code>, <code>ToPropertyDescriptor</code></li>\n<li>[New] ES2015+: add <code>CompletePropertyDescriptor</code>, <code>Set</code>, <code>HasOwnProperty</code>, <code>HasProperty</code>, <code>IsConcatSpreadable</code>, <code>Invoke</code>, <code>CreateIterResultObject</code>, <code>RegExpExec</code></li>\n<li>[Fix] es7/es2016: do not mutate ES6</li>\n<li>[Fix] assign helper only supports one source</li>\n<li>[Deps] update <code>is-regex</code></li>\n<li>[Dev Deps] update <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code></li>\n<li>[Dev Deps] update <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>nsp</code>, <code>semver</code>, <code>tape</code></li>\n<li>[Tests] add tests for missing and excess operations</li>\n<li>[Tests] add codecov for coverage</li>\n<li>[Tests] up to <code>node</code> <code>v8.2</code>, <code>v7.10</code>, <code>v6.11</code>, <code>v4.8</code>; newer npm breaks on older node</li>\n<li>[Tests] use same lists of value types across tests; ensure tests are the same when ops are the same</li>\n<li>[Tests] ES2015: add ToNumber symbol tests</li>\n<li>[Tests] switch to <code>nyc</code> for code coverage</li>\n<li>[Tests] make IsRegExp tests consistent across editions</li>\n</ul>\n<h1>1.7.0 / 2017-01-22</h1>\n<ul>\n<li>[New] ES6: Add <code>GetMethod</code> (#16)</li>\n<li>[New] ES6: Add <code>GetV</code> (#16)</li>\n<li>[New] ES6: Add <code>Get</code> (#17)</li>\n<li>[Tests] up to <code>node</code> <code>v7.4</code>, <code>v6.9</code>, <code>v4.6</code>; improve test matrix</li>\n<li>[Dev Deps] update <code>tape</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>safe-publish-latest</code></li>\n</ul>\n<h1>1.6.1 / 2016-08-21</h1>\n<ul>\n<li>[Fix] ES6: IsConstructor should return true for <code>class</code> constructors.</li>\n</ul>\n<h1>1.6.0 / 2016-08-20</h1>\n<ul>\n<li>[New] ES5 / ES6: add <code>Type</code></li>\n<li>[New] ES6: <code>SpeciesConstructor</code></li>\n<li>[Dev Deps] update <code>jscs</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>semver</code>; add <code>safe-publish-latest</code></li>\n<li>[Tests] up to <code>node</code> <code>v6.4</code>, <code>v5.12</code>, <code>v4.5</code></li>\n</ul>\n<h1>1.5.1 / 2016-05-30</h1>\n<ul>\n<li>[Fix] <code>ES.IsRegExp</code>: actually look up <code>Symbol.match</code> on the argument</li>\n<li>[Refactor] create <code>isNaN</code> helper</li>\n<li>[Deps] update <code>is-callable</code>, <code>function-bind</code></li>\n<li>[Deps] update <code>es-to-primitive</code>, fix ES5 tests</li>\n<li>[Dev Deps] update <code>jscs</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>tape</code>, <code>nsp</code></li>\n<li>[Tests] up to <code>node</code> <code>v6.2</code>, <code>v5.11</code>, <code>v4.4</code></li>\n<li>[Tests] use pretest/posttest for linting/security</li>\n</ul>\n<h1>1.5.0 / 2015-12-27</h1>\n<ul>\n<li>[New] adds <code>Symbol.toPrimitive</code> support via <code>es-to-primitive</code></li>\n<li>[Deps] update <code>is-callable</code>, <code>es-to-primitive</code></li>\n<li>[Dev Deps] update <code>jscs</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>semver</code>, <code>tape</code></li>\n<li>[Tests] up to <code>node</code> <code>v5.3</code></li>\n</ul>\n<h1>1.4.3 / 2015-11-04</h1>\n<ul>\n<li>[Fix] <code>ES6.ToNumber</code>: should give <code>NaN</code> for explicitly signed hex strings (#4)</li>\n<li>[Refactor] <code>ES6.ToNumber</code>: No need to double-trim</li>\n<li>[Refactor] group tests better</li>\n<li>[Tests] should still pass on <code>node</code> <code>v0.8</code></li>\n</ul>\n<h1>1.4.2 / 2015-11-02</h1>\n<ul>\n<li>[Fix] ensure <code>ES.ToNumber</code> trims whitespace, and does not trim non-whitespace (#3)</li>\n</ul>\n<h1>1.4.1 / 2015-10-31</h1>\n<ul>\n<li>[Fix] ensure only 0-1 are valid binary and 0-7 are valid octal digits (#2)</li>\n<li>[Dev Deps] update <code>tape</code>, <code>jscs</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code></li>\n<li>[Tests] on <code>node</code> <code>v5.0</code></li>\n<li>[Tests] fix npm upgrades for older node versions</li>\n<li>package.json: use object form of \"authors\", add \"contributors\"</li>\n</ul>\n<h1>1.4.0 / 2015-09-26</h1>\n<ul>\n<li>[Deps] update <code>is-callable</code></li>\n<li>[Dev Deps] update <code>tape</code>, <code>jscs</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code></li>\n<li>[Tests] on <code>node</code> <code>v4.2</code></li>\n<li>[New] Add <code>SameValueNonNumber</code> to ES7</li>\n</ul>\n<h1>1.3.2 / 2015-09-26</h1>\n<ul>\n<li>[Fix] Fix <code>ES6.IsRegExp</code> to properly handle <code>Symbol.match</code>, per spec.</li>\n<li>[Tests] up to <code>io.js</code> <code>v3.3</code>, <code>node</code> <code>v4.1</code></li>\n<li>[Dev Deps] update <code>tape</code>, <code>jscs</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>semver</code></li>\n</ul>\n<h1>1.3.1 / 2015-08-15</h1>\n<ul>\n<li>[Fix] Ensure that objects that <code>toString</code> to a binary or octal literal also convert properly</li>\n</ul>\n<h1>1.3.0 / 2015-08-15</h1>\n<ul>\n<li>[New] ES6â€™s ToNumber now supports binary and octal literals.</li>\n<li>[Dev Deps] update <code>jscs</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>tape</code></li>\n<li>[Docs] Switch from vb.teelaun.ch to versionbadg.es for the npm version badge SVG</li>\n<li>[Tests] up to <code>io.js</code> <code>v3.0</code></li>\n</ul>\n<h1>1.2.2 / 2015-07-28</h1>\n<ul>\n<li>[Fix] Both <code>ES5.CheckObjectCoercible</code> and <code>ES6.RequireObjectCoercible</code> return the value if they don't throw.</li>\n<li>[Tests] Test on latest <code>io.js</code> versions.</li>\n<li>[Dev Deps] Update <code>eslint</code>, <code>jscs</code>, <code>tape</code>, <code>semver</code>, <code>covert</code>, <code>nsp</code></li>\n</ul>\n<h1>1.2.1 / 2015-03-20</h1>\n<ul>\n<li>Fix <code>isFinite</code> helper.</li>\n</ul>\n<h1>1.2.0 / 2015-03-19</h1>\n<ul>\n<li>Use <code>es-to-primitive</code> for ToPrimitive methods.</li>\n<li>Test on latest <code>io.js</code> versions; allow failures on all but 2 latest <code>node</code>/<code>io.js</code> versions.</li>\n</ul>\n<h1>1.1.2 / 2015-03-20</h1>\n<ul>\n<li>Fix isFinite helper.</li>\n</ul>\n<h1>1.1.1 / 2015-03-19</h1>\n<ul>\n<li>Fix isPrimitive check for functions</li>\n<li>Update <code>eslint</code>, <code>editorconfig-tools</code>, <code>semver</code>, <code>nsp</code></li>\n</ul>\n<h1>1.1.0 / 2015-02-17</h1>\n<ul>\n<li>Add ES7 export (non-default).</li>\n<li>All grade A-supported <code>node</code>/<code>iojs</code> versions now ship with an <code>npm</code> that understands <code>^</code>.</li>\n<li>Test on <code>iojs-v1.2</code>.</li>\n</ul>\n<h1>1.0.1 / 2015-01-30</h1>\n<ul>\n<li>Use <code>is-callable</code> instead of an internal function.</li>\n<li>Update <code>tape</code>, <code>jscs</code>, <code>nsp</code>, <code>eslint</code></li>\n</ul>\n<h1>1.0.0 / 2015-01-10</h1>\n<ul>\n<li>v1.0.0</li>\n</ul>","docPath":"argo-ci/node_modules/es-abstract/changelog","proj":"argo-ci"}}

